:root {
  --bg-color: #d7d4d4;
  --text-color: #000;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html,
body {
  margin: 0;
  padding: 0;
  background: var(--bg-color);
  color: var(--text-color);
  font-family: "Nunito", sans-serif;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
  margin-bottom: 0.5em;
}

h1 {
  font-size: 1.8rem;
}
@media (min-width: 700px) {
  h1 {
    font-size: 2.25rem;
  }
}
@media (min-width: 900px) {
  h1 {
    font-size: 2.625rem;
  }
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, auto);
  grid-gap: 2.5em;
  width: min(100% - 2.5em, 1000px);
  margin-inline: auto;
}
@media (min-width: 900px) {
  .grid {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto;
  }
}
.grid__main, .grid__sidebar {
  padding: 2.5em;
}
.grid__main {
  background: #feda5f;
}
@media (max-width: 699.98px) {
  .grid__main {
    background: #000;
    color: #fff;
  }
}
.grid__sidebar {
  background: #000;
  color: #fff;
}
@media (max-width: 899.98px) {
  .grid__sidebar {
    background: #e0dbdb;
    color: #000;
  }
}
.grid__widget {
  padding: 1em;
}

img {
  width: 100%;
}